
html{
  font-size: 15px;
}
$button-size:3.5rem;
$transition:.3s;
$color: #fff;

.lines-button{
  display: inline-block;
  padding: $button-size/2 $button-size/4;
  transition: .3s;
  cursor: pointer;
  user-select:none;
  border-radius: $button-size/7;
  &:hover{
    opacity: 1;
  }
  &:active{
    transition: 0;
    background: rgba(0,0,0,.1);
  }
}
//$button-height:43.3px;
$button-width:100%;
$line-height:$button-size/7;
$button-width:2.9rem;
$button-height:5rem;
@mixin line {
  display: inline-block;
  width: $button-size;
  height: $line-height;
  background:$color;
  border-radius: $button-size/14;
  transition: $transition;
}
.lines{
  @include line;
  position: absolute;
   top:$button-height/4 ;
   left: $button-height/7 ;
  &::before,&::after{
    @include line;
    position: absolute;
    left: 0;
    content: "";
    transform-origin: $button-size/14 center;
  }
  &::before{
    top:$button-size/4;

  }
  &::after{
    top: -$button-size/4;
  }
}

.lines.close  {
  background: transparent;
  &::before,&::after{
    transform-origin: 50% 50%;
    top: 0;
    width: $button-size;
  }
  &::before{
  transform: rotate3d(0,0,1,45deg);
  }
  &::after{
    transform: rotate3d(0,0,1,-45deg);
  }
}
 //hexagon
 .hexagon {
  position: relative;
  width: 75px;
  height: 43.30px;
  background-color: #64C7CC;
  margin: 100px;
}

.hexagon:before,
.hexagon:after {
  content: "";
  position: absolute;
  width: 0;
  border-left: 37.5px solid transparent;
  border-right: 37.5px solid transparent;
}

.hexagon:before {
  bottom: 100%;
  border-bottom: 21.65px solid #64C7CC;
}

.hexagon:after {
  top: 100%;
  width: 0;
  border-top: 21.65px solid #64C7CC;
}

.button{
  position: relative;
  width: $button-width;
  height: $button-height;
}
